/* productContainer 스타일 */
.productContainer {
    display: flex;
    flex-direction: row;
    margin-top: 180px;
    width:100%;
    height: auto;
    justify-content: center;
}

.sideMenu{
    width:180px; 
    height: 280px;
    background-color: #fff;
    box-shadow: 1px 1px 6px gray;
}
.clip{
    position: absolute;
    top: -45px;
    left: 50px;
}

.sideMenuLine{
    padding-top: 20px;
    width : 160px; 
    height : 238px;
    border: 1px solid #BA7C39;
    margin-top:10px; 
    margin-left:10px;
    color: #706F6F;
    display: flex;              /* Flexbox로 자식 요소들을 배치 */
    flex-direction: column;     /* 세로 방향으로 배치 */
    gap: 20px;   
}

.sideMenuLine a:hover{
    color:#BB6C33;
    cursor: pointer;
}
.productTitle span{
    display:inline-block;
    color:#BB6C33;
}

.productTitle div{
    width: 800px;
    height: 1px;
    background-color: #BA7C39;
    display: inline-block; 
    margin-bottom: 4px;
    margin-left: 10px;
}


.products{
    width: 1000px;
    margin-top: 30px;
    margin-left: 100px;
}
.productRow{
    display: flex;
    justify-content: flex-start; /* 아이템들이 왼쪽으로 정렬되도록 */
}
.productItem{
    margin: 17px;
    box-shadow: 1px 1px 6px gray;
    color: #BA7C39;
    cursor: pointer;
}
.products img {
    width: 190px;
}