.container {
    margin-top: 80px;
    width: 100%;
}

.mainBannerFrame {
    display: flex;
    position: relative;
}

.mainBanner {
    width: 100%;
    height: 500px;
    object-fit: cover; /* 수정: objectFit → object-fit */
    position: absolute;
    top: 0px;
    left: 0px;
}

.mainBannerBack {
    width: 100%;
    height: 500px;
    background-color: #424241; /* 수정: backgroundColor → background-color */
}

.subBannerFrame {
    display: flex;
    justify-content: center; /* 수정: justifyContent → justify-content */
    align-items: center; /* 수정: alignItems → align-items */
    margin-top: 70px; /* 수정: marginTop → margin-top */
}

.subBanner1 {
    width: 400px;
    margin-right: 15px;
}

.subBanner2 {
    width: 700px;
    height: 400px;
    background-color: #fff; /* 수정: backgroundColor → background-color */
    margin-left: 15px;
    display: flex;
    justify-content: center; /* 수정: justifyContent → justify-content */
    align-items: center; /* 수정: alignItems → align-items */
    box-shadow: 1px 1px 6px gray;
}

.sub2Inner {
    width: 668px;
    height: 368px;
    border: 3px solid #EDD6C0;
}
.subTitle img {
    float:left;
    margin-left: 20px;
}
.bannerProducts{
    width: 668px;
    height: 280px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.sub2Img{
    width:158px;
    margin : 0 25px 0 25px;
}

.explan{
    font-size: 12px;
    color: #706F6F;
    margin:15px 0 5px 0;
}

.name{
    font-size: 16px;
    color: #BA7C39;
    margin:0;
}

.circleButtons {
    display: flex;
    justify-content: center;
  }
  
  .circleButtons button {
    width: 15px;
    height: 15px;
    border-radius: 50%;
    border: 1px solid #ccc;
    margin: 0 5px;
    background-color: #fff;
    cursor: pointer;
  }
  
  .circleButtons button.active {
    background-color: #BA7C39; /* 강조할 색상 */
    color: white;
  }
  
  .circleButtons button:hover {
    background-color: #ddd;
  }