

.headerContainer{
    width: 100%;
    height: 80px;
    background-color: #424241;
    position: fixed;
    top: 0;
    z-index: 1000;
    display: flex;
    align-items: center;
}
.innerContainer {
    width: 100%;
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    justify-content: space-between;
    padding-right: 10px;
}



.line {
    background-color: #BA7C39;
    width: 100%;
    height: 2px;
    position: absolute;
    bottom: 10px;
}
.snsFrame {
    margin-left: 60px;
}
.snsImage {
    width: 18px;
    margin-left: 30px;
    cursor: pointer;
}
.logo {
    width: 230px;
    margin-left: 50px;
}
.menufont {
    color: #fff;
    font-size: 15px;
    cursor: pointer;
    padding: 40px;
    text-decoration: none;
    display:flex;
    justify-content:center;
}
.menufont:hover {
    color: #BA7C39;
}
.headerLoginSignupFrame{
    margin-right: 80px;
}
.loginSignup {
    font-size: 12px;
    color: #fff;
    margin: 0 7px;
    text-decoration: none;
}

.loginSignup:hover{
    color: #BA7C39;
}





.dropdownMenu{
    
    background-color: rgba(255, 255, 255, 0.8);
    margin-top:55px;
    padding: 10px 0;
    position:absolute;
    width: 170px;
    transition: all 0.3s ease-in-out; 
    z-index: 999; 
    display: flex;              /* Flexbox로 자식 요소들을 배치 */
    flex-direction: column;     /* 세로 방향으로 배치 */
    gap: 10px;                  /* 항목 간 간격 */
    
}

.dropdownItem{
    color: #706F6F;
    padding: 10px 20px;
    cursor: pointer;
    text-align: center;
    display: block; /* Link를 block 요소로 설정하여 세로로 정렬되도록 */
    padding: 10px;
    text-decoration: none;  /* 링크의 기본 밑줄 제거 */
}

.dropdownItem:hover{
    color: #BA7C39;
}