.loginContainer{
    width:100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;

}
.loginContainer > div{
    width: 800px;
    height: 530px;
    background-color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 80px;
    box-shadow: 1px 1px 6px gray;
}

.loginContainerLine{
    border: 1px solid #BA7C39;
    width: 770px;
    height: 500px;
}   

.loginTitle{
    color: #BB6C33;
    font-size: 25px;
}

.loginContainerLine img{
    width: 23px;
    margin-right: 10px;
}

.loginSuggest{
    color:#BA7C39;
    font-size: 16px;
}
.commonLogin{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.commonLogin input{
    width: 350px;
    height: 36px;
    border-radius: 5px;
    border: 2px solid #B2B2B2;
    margin: 10px 30px 10px 0;
    padding-left: 10px;
}
.commonLogin button{
    width: 100px;
    height: 100px;
    border-radius: 5px;
    border: none;
    background-color: #BA7C39;
    color: #fff;
    cursor: pointer;
}
.commonLogin > div{
    display: flex;
    flex-direction: column;
}
.commonLogin button:hover{
    background-color: #9B6734;
}

.socialLogin{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
}

.socialLogin button {
    width: 496px;
    height: 40px;
    border-radius: 5px;
    margin : 10px 0 10px 0;
    border: none;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.kakaoLogin:hover{
    background-color: #F4973D;
}

