.brandContainer{
    width:100%;
    border: 1px solid #000;
    margin-top: 80px;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.brandLogo{
    width: 250px;
    margin: 80px 0 80px 0;

}

.brandExplan img{
    width: 380px;
    margin : 0 20px 0 20px;
}

.brandExplan {
    text-align: left;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 80px;
}

.brandText{
    width:500px;
    margin : 0 20px 0 20px;
    position: relative;
    height: 250px;
    display: flex;
    justify-content: center;
    flex-direction: column;
}
.brandText p{
    color: #BA7C39;
    font-size: 20px;
}

.brandText span{
    color: #575756;
    font-size: 14px;
    line-height: 2;
    margin-bottom: 15px;
}

.brandLineTop ,.brandLineBottom{
    width: 35px;
    height: 2px;
    background-color: #BA7C39;
}

.brandLineTop{
    position: absolute;
    top:0px;
}

.brandLineBottom{
    position: absolute;
    bottom:0px;
}