
.signupContainer{
    width:100%;
    height: 100vh;
    display: flex;
    justify-content: center;

}

.signupContainer > div{
    width: 800px;
    height: 730px;
    background-color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 80px;
    box-shadow: 1px 1px 6px gray;
}

.signupContainerLine{
    border: 1px solid #BA7C39;
    width: 770px;
    height: 700px;
    display: flex;
    justify-items: center;
    align-items: center;
    flex-direction: column;
}

.signupTitle{
    color: #BB6C33;
    font-size: 25px;
}

.signupTitle img{
    width: 29px;
    margin-right: 10px;
    
}

.signupInput{
    display: flex;
    flex-direction: column;
    margin-top: 40px;
    width: 495px;
}

.signupInput input{
    width: 350px;
    height: 35px;
    border-radius: 5px;
    border: 2px solid #B2B2B2;
    padding-left: 10px;
    margin : 0 25px 20px 0;
}


.signupInput button{
    width: 100px;
    height: 42px;
    border: none;
    border-radius: 5px;
    background-color: #BA7C39;
    color : #fff;
}

.signupInput button:hover{
    background-color: #9B6734;
}